* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

header {
  padding: 50px 15px 0px;
}
body {
  background-color: lightgray !important;
}
header h1 {
  color: black;
  font-weight: 500;
  text-align: center;
}
.center-text {
  text-align: center;
}

main {
  width: 100%;
  max-width: 960px;
  margin: 0 auto;
}

section {
  padding: 15px;
}

.searchbox {
  display: block;
  width: 100%;
  padding: 15px;

  border: none;
  outline: none;
  background: none;

  background-color: #eee;
  border-radius: 8px;

  color: #53565a;
  font-size: 20px;
  font-weight: 300;

  transition: 0.4s ease-out;
}

.searchbox:focus {
  box-shadow: 0px 0px 1px 1px grey;
  border-radius: 8px;
}

.results {
  display: flex;
  flex-wrap: wrap;
  margin: 0px -15px;
}

.results .result {
  padding: 15px;
  display: flex;
  flex-direction: column;
  cursor: pointer;
}
.results .result:hover {
  -webkit-transform: scale(1.03);
  transform: scale(1.03);
  background-color: none;
}

.results .result img {
  display: block;
  width: 100%;
}
.results .result img:hover {
  box-shadow: 0px 0px 2px 0px grey;
  /* -webkit-transform: scale(1.03);
  transform: scale(1.03);
  box-shadow: 0px 10px 25px rgba(0, 0, 0, 0.08); */
}

.results .result h3,
.results .empty h3 {
  color: black;
  font-size: 20px;
  font-weight: 600;
  padding: 10px;
  background: #ebedef;
  transition: 0.4s ease-out;
}

.results .result:hover h3 {
  background: #8cbae7;
  color: white;
  box-shadow: 0px 0px 2px 0px grey;
}
.empty {
  padding: 15px;
  display: flex;
  flex-direction: column;
  cursor: pointer;
  text-align: center;
  margin: 0 auto;
}

.popup {
  position: fixed;
  top: 0;
  left: 0;
  width: 100vw;
  height: 100vh;
  background-color: #96a5b4ed;

  overflow-y: scroll;
}

.popup .content {
  display: block;
  width: 100%;
  max-width: 960px;
  margin: 0 auto;
  padding: 25px;

  color: #fff;
}

.popup .content h2 {
  font-size: 28px;
  font-weight: 600;
}
.popup .content h2 span {
  font-weight: 300;
  font-size: 20px;
}

.popup .content .rating {
  margin-bottom: 30px;
}

.popup .content .plot {
  display: flex;
  flex-wrap: wrap;
  margin: 0px -15px 30px;
}

.popup .content .plot img {
  flex: 1 1 50%;
  max-width: 300px;
  padding: 0px 15px;
}

.popup .content .plot p {
  flex: 1 1 50%;
  padding: 15px;
}

.popup .content .close {
  display: inline-block;
  color: #fff;
  border: none;
  outline: none;
  appearance: none;
  cursor: pointer;

  transition: 0.4s ease-out;
}
.popup .content .close span {
  font-size: 42px;
}

.popup .content .close:hover {
  color: #fff;
}
